import Vue from 'vue';
import Component from 'vue-class-component';
import AeModal from '../../index';

Vue.use(DMessage);
Vue.use(AeModal);
@Component({
  name: 'Demo1'
})
export default class Demo1 extends Vue {

  public show = false;
  public treeData = [{
    title: '1',
    key: '1',
    children: [{
      title: '3',
      key: '3',
      isLeaf: true
    }]
  }, {
    title: '2',
    key: '2',
    children: [],
    type: 'dir'
  }];

  public cancelClicked() {
    this.$message.error('cancel clicked');
  }

  public loadTreeData(node) {
    return new Promise(() => {
    });
  }

  public onOk() {
    this.$message.success('ok clicked');
    this.show = false;
  }

  public async showAlert() {
    try {
      await this.$dalert({
        content: '<div>确认</div>',
        dangerousHtml: true
      });
      this.$dalert('确认');
    } catch (e) {
      this.$dalert('取消');
    }
  }

  public async showConfirm() {
    try {
      await this.$dconfirm('确认操作吗？', '确认');
      this.$dalert('确认操作');
    } catch (e) {
      this.$dalert('取消操作');
    } finally {

    }
  }

  public async showPrompt() {
    try {
      const v = await this.$dprompt({
        content: '<div>你好</div>',
        title: '提示',
        inputValue: '23',
        dangerousHtml: true,
        required: true
      });
      if (v === '') {
        throw new Error('必须输入');
      } else {
        this.$message.info('the input value is : ' + v);
      }
    } catch (e) {
    } finally {

    }
  }
}
